import {Person} from '../@types/common';
import dayjs from 'dayjs';
interface Props {
    type : string,
    data : Person[]
}
export default function TableView({type, data} : Props){
    return (
        <div className='mt-2 ml-[80px] h-[500px] overflow-scrollY'>
            {
                type === '01' &&
                <div className='min-w-[1400px] flex_center px-4'>
                    {
                        data.length > 0 ?
                            <table className="table-auto w-full border shadow ">
                                <thead className='h-[45px] font-[500]'>
                                <tr className='border-b pb-2'>
                                    <th>
                                        No.
                                    </th>
                                    <th>
                                        
                                    </th>
                                    <th>
                                        
                                    </th>
                                    <th>
                                        
                                    </th>
                                    <th>
                                        
                                    </th>
                                    <th>
                                        ()
                                    </th>
                                    <th>
                                        
                                    </th>
                                </tr>
                                </thead>
                                <tbody className='base_font bg-white'>
                                {
                                    data.map((person, index) =>
                                        <tr key={index}>
                                            <td className='text-center h-[40px] border-b'>
                                                {index + 1}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.cmpk}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.cName}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.cCell}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.cJumin}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.age}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {dayjs(person.createdYMD).format('YYYY MM DD')}
                                            </td>
                                        </tr>
                                    )
                                }
                                </tbody>
                            </table>
                            :
                            <div className='w-full flex_center'>
                                <div
                                    className='w-full h-[60px] bg-white border rounded-xl mb-8 shadow px-4 flex_center'>
                                    <p>  .</p>
                                </div>
                            </div>
                    }
                </div>
            }


            {
                type === '02' &&
                <div className='min-w-[1400px] flex_center px-4'>
                    {
                        data.length > 0 ?
                            <table className="table-auto w-full border shadow ">
                                <thead className='h-[45px] font-[500]'>
                                <tr className='border-b pb-2'>
                                    <th>
                                        No.
                                    </th>
                                    <th>
                                        
                                    </th>
                                    <th>
                                        
                                    </th>
                                    <th>
                                        
                                    </th>
                                    <th>
                                        
                                    </th>
                                    <th>
                                        ()
                                    </th>
                                    <th>
                                        (1)
                                    </th>
                                    <th>
                                        (2)
                                    </th>
                                    <th>
                                        
                                    </th>
                                    <th>
                                        
                                    </th>
                                </tr>
                                </thead>
                                <tbody className='base_font bg-white'>
                                {
                                    data.map((person, index) =>
                                        <tr key={index}>
                                            <td className='text-center h-[40px] border-b'>
                                                {index + 1}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.cmpk}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.cName}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.cCell}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.cJumin}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.age}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.appointDT1st !== '' ? dayjs(person.appointDT1st).format('YYYY MM DD') : ''}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.appointDT2nd !== '' ? dayjs(person.appointDT2nd).format('YYYY MM DD') : ''}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {person.saleStatus}
                                            </td>
                                            <td className='text-center h-[40px] border-b'>
                                                {dayjs(person.createdYMD).format('YYYY MM DD')}
                                            </td>
                                        </tr>
                                    )
                                }
                                </tbody>
                            </table>
                            :
                            <div className='w-full flex_center'>
                                <div
                                    className='w-full h-[60px] bg-white border rounded-xl mb-8 shadow px-4 flex_center'>
                                    <p>  .</p>
                                </div>
                            </div>
                    }
                </div>
            }
        </div>
    )
}
